var request = require('../lib/request');
var moment = require('moment-timezone');
var modalActions = require('./modals');
var userActions = require('./users');
var editorActions = require('./editor');
var parsePagination = require('../lib/parse_pagination');
var { Delta } = require('rich-text');

export function loadClippings(query) {
  query.community_id = app.state.get('community', 'id');
  query.includes = 'user';
  query.order = 'created_on desc';
  query.per_page = 10;

  return request('/api/chorus/clippings', { query: query }).then(function(response) {
    var pagination = parsePagination({
      page: query.page,
      perPage: 10,
      total: parseInt(response.item_count)
    });

    var clippings = response._embedded.clipping.map((clipping) => {
      var user = clipping._embedded.user;
      return Object.assign(clipping, {
        clipped_by: userActions.userLabel(user),
        date: moment(clipping.created_on).format(app.config.dateFormat)
      });
    });

    return [clippings, pagination];
  });
}

function archiveClipping({ id }) {
  return request(`/api/chorus/clippings/${id}`, {
    method: 'PUT',
    query: { community_id: app.state.get(['community', 'id']) },
    body: { archived: true }
  });
}

export function selectClipping(target = {}) {
  modalActions.pushModal({ selectClipping: { target } });
}

export function editClipping(data, label, target = {}) {
  modalActions.pushModal({ editObject: { data, label, target, type: 'clipping' } });
}

export function storeSelectClipping(results) {
  app.state.set(['modal', 'selectClipping', 'results'], results);
}

export function batchInsert(clippings) {
  return Promise.all(
    clippings.map((clipping) => {
      insertClipping(clipping);
    })
  );
}
export function insertClipping(clipping) {
  var docId = app.state.get(['doc', 'id']);

  var delta = new Delta();
  var description = clipping.notes.trim();
  delta.insert(clipping.title.trim(), { link: clipping.url });
  if (description) {
    delta.insert('\n', { br: true }).insert(`${description}\n`);
  } else {
    delta.insert('\n');
  }
  editorActions.insertAtCaret(docId, delta);
  return archiveClipping(clipping);
}
