// A small library for running internal searches on local collections, used by
// a number of autocomplete components. Expects the following inputs:
//
//  collection {Array<Object>} - An array of items in the collection. Each item
//    is an object with at least `id` and `name` attributes.
//  selected {Array<Number> | Number} - Either an array of selected id, or one
//    selected id
//  value {String} - Text value for matching
export default function searchCollection(collection, selected, value) {
  var searchValue = value.toLowerCase();
  var exactMatches = [];
  var startsWithMatches = [];
  var containsMatches = [];

  // Go through the collection and record different matches
  for (let item of collection) {
    var itemName = item.name.toLowerCase();
    var matchIndex = itemName.indexOf(searchValue);
    if (!isSelected(item.id, selected) && matchIndex > -1) {
      if (itemName === searchValue) {
        exactMatches.push(item);
      } else if (matchIndex === 0) {
        startsWithMatches.push(item);
      } else {
        containsMatches.push(item);
      }
    }
  }

  return exactMatches.concat(startsWithMatches, containsMatches);
}

function isSelected(id, selected) {
  return Array.isArray(selected) ? selected.indexOf(id) > -1 : selected === id;
}
