export const AllOrganizationNetworks = `
  query AllOrganizationNetworks($id: Int!) {
    organization(id: $id) {
      networks {
        id
        name
        isDisabled
        isMothballed
        isPrivateMode
      }
    }
  }
`;

export const AllNetworkCommunities = `
  query AllNetworkCommunities($networkId: Any, $page: Int!) {
    searchCommunities(query: {
      whereAll: [{ field: "networkId", equal: $networkId }]
      publicOnly: false
      perPage: 100
      pageNumber: $page
    }) {
      totalCount
      results {
        id
        name
      }
    }
  }
`;
