var { escape: escapeHtml } = require('lodash');
var { Delta } = require('rich-text');
var toInlineHtml = require('convert-rich-text').toInlineHtml;
var request = require('../lib/request');
var requestProgress = require('../lib/request_progress');
var HttpStatusError = require('../lib/errors/http_status_error');
var sentryActions = require('./sentry');
var autosave = require('./autosave');
var messages = require('../config/messages');

const DEFAULT_PAYLOAD = {
  columns: [],
  rows: [],
  upload_filename: '',
  google_spreadsheet_url: ''
};

// ############ Import via Google Sheets ############

export function importSheetData(url) {
  // If no Google Sheets ID could be pulled from the URL, return an error
  const { id, sheetId } = getSheetId(url);
  if (!id) {
    const error = new Error(messages.INVALID_SPREADSHEET_URL);
    return Promise.reject(error);
  }

  return request(`/api/google/sheets/${id}`, {
    query: {
      includeGridData: 1,
      // get partial response of attributes to help with performance
      fields: [
        'sheets/properties/sheetId',
        'sheets/data/rowData/values(textFormatRuns,formattedValue,hyperlink)',
        'sheets/data/rowData/values/effectiveFormat(hyperlinkDisplayType,textFormat)',
      ].join(','),
      prettyPrint: 0,
    },
    opts: { handleError: false }
  }).then(sheetData => {
    var { columns, rows } = stripSheetData(sheetData, sheetId);
    return parseTableData(columns, rows, parseSheetCell);
  }).then(data => {
    var payload = Object.assign({}, DEFAULT_PAYLOAD, data, { google_spreadsheet_url: url });
    return saveTableData(payload);
  }).catch(err => {
    if (err instanceof HttpStatusError && err.response.status === 403) {
      err.message = messages.SHEET_PERMISSIONS;
      return Promise.reject(err);
    } else {
      sentryActions.reportError(err);
    }
  });
}

// Strips out rows with all empty cells
function stripSheetData(sheetData, sheetId) {
  var sheet = getTargetSheet(sheetData, sheetId);
  var rowData = sheet.data[0].rowData;
  var columns = rowData[0].values;

  var rows = rowData.slice(1).reduce((result, row) => {
    if (row.values && row.values.some(cell => cell.formattedValue)) {
      result.push(row.values);
    }
    return result;
  }, []);

  return { columns, rows };
}

// Gets the target sheet if a sheetId is present, if not returns the first sheet
function getTargetSheet(data, id) {
  var sheet = data.sheets.find(sheet => sheet.properties.sheetId === id);
  return sheet || data.sheets[0];
}

// Take a cell object and turn it into a formatted string:
// A) If the object is empty, return an empty string
// B) Only bold a cell if it isn't a header (headers are automatically bolded)
// C) Only apply the underline format if the cell isn't hyperlinked
function parseSheetCell(cell, isHeader = false) {
  if (!cell || !cell.formattedValue) {
    return '';
  }

  var cellText = cell.textFormatRuns ? parseCellOps(cell, isHeader) : cell.formattedValue;
  var formats = cell.effectiveFormat.textFormat;
  if (formats) {
    if (formats.bold && !isHeader) {
      cellText = `<b>${cellText}</b>`;
    }
    if (formats.italic) {
      cellText = `<i>${cellText}</i>`;
    }
    if (formats.strikethrough) {
      cellText = `<strike>${cellText}</strike>`;
    }
  }

  if (cell.effectiveFormat.hyperlinkDisplayType === 'LINKED' && cell.hyperlink) {
    cellText = `<a href="${cell.hyperlink}">${cellText}</a>`;
  }

  return cellText;
}

// Translates Google cell "runs" (i.e. ops for character-level formatting)
// into a rich-text Delta, which is fed into toInlineHtml for conversion to markup
function parseCellOps({ formattedValue: text, textFormatRuns: ops }, isHeader = false) {
  var allowedFormats = ['italic', 'strikethrough'];
  if (!isHeader) {
    allowedFormats.push('bold');
  }

  let delta = new Delta();
  for (let [i, op] of ops.entries()) {
    const next = ops[i + 1] || {};
    const start = op.startIndex || 0;
    const end = next.startIndex || text.length;
    const formats = getFormats(op.format, allowedFormats);

    delta.insert(text.slice(start, end), formats);
  }

  return toInlineHtml(delta);
}

function getFormats(formats, allowedFormats) {
  return Object.keys(formats).reduce((result, key) => {
    if (allowedFormats.indexOf(key) > -1) {
      var resultKey = key === 'strikethrough' ? 'strike' : key;
      result[resultKey] = formats[key];
    }
    return result;
  }, {});
}

function getSheetId(url) {
  var id = url.match(/\/spreadsheets\/d\/([a-zA-Z0-9-_]+)/);
  id = id ? id[1] : null;

  var sheetId = url.match(/gid=(\d+)/);
  sheetId = sheetId ? parseInt(sheetId[1]) : null;

  return { id, sheetId };
}

// ############ File Upload ############

export function uploadData(file, onProgress) {
  var formData = new FormData();
  formData.append('file', file);

  var csvFileName;
  return requestProgress('/api/csv', 'POST', formData, onProgress).then(({ data, upload_filename: filename }) => {
    var columns = data[0];
    var rows = data.slice(1);
    csvFileName = filename;
    return parseTableData(columns, rows);
  }).then(data => {
    var payload = Object.assign({}, DEFAULT_PAYLOAD, data, { upload_filename: csvFileName });
    return saveTableData(payload);
  }).catch(parseUploadError);
}

// Try to parse the upload error to see if it's a file too large issue
function parseUploadError(error) {
  if (error instanceof HttpStatusError) {
    try {
      var response = JSON.parse(error.response.response);
      if (response.message === 'File too large') {
        error.message = messages.CSV_TOO_LARGE;
        return Promise.reject(error);
      } else {
        throw error;
      }
    } catch (err) {
      sentryActions.reportError(err);
    }
  } else {
    sentryActions.reportError(error);
  }
}

// ############ Shared Parsing & Saving Logic ############

// Resets the columns and rows data in state for the table
export function saveTableData(data) {
  var contents = app.state.select(['subdoc', 'head', 'contents']);
  var delta = Object.keys(data).reduce((result, key) => {
    result.push({
      p: [key],
      od: contents.get(key),
      oi: data[key]
    });
    return result;
  }, []);

  autosave.localUpdate(app.state.get(['subdoc', 'id']), delta);
}

// The main parser function. Takes column data, row data, and an optional parser
// function for individual cells (used for Google Sheets). Returns:
//
//   columns: Array<String>
//   rows:    Array<Array<String>>
function parseTableData(columns, rows, parseCell = escapeHtml) {
  // Parse each column header, unless the header is empty. If a column header
  // is empty, by convention we strip out that column from the table data.
  var parsedColumns = columns.reduce((result, cell) => {
    var parsed = parseCell(cell, true);
    if (parsed) {
      result.push(parsed);
    }
    return result;
  }, []);

  // Parse each cell in each row. Only include cells in columns with headers. We
  // iterate based on the columns array length to ensure each row cell is
  // included, even if it's a blank cell.
  const parsedRows = rows.map(row => {
    let result = [];
    for (let [i, column] of columns.entries()) {
      if (parseCell(column)) {
        result.push(parseCell(row[i]));
      }
    }
    return result;
  });

  return { columns: parsedColumns, rows: parsedRows };
}
