const request = require('../lib/request');
const normalizeUrl = require('../../shared/normalize_url');
const entryActions = require('../actions/entry');

export function getMetadata(links) {
  return Promise.all(links.map(async (link) => {
    // workaround for firefox inserting a linebreak when typing in an autofocused quill field
    const title = link.title.replace('<br>', '');
    const url = link.url.replace('<br>', '');
    // don't process empty url fields
    if (!url) {
      return;
    }

    try {
      const entry = await entryActions.getEntryByUrl(url);
      // Use custom title if provided, otherwise fall back to the headline or entry title fields
      // If the entry is not published return original short link provided
      return {
        title: title || entry.promo_headline_field || entry.title.replace(/&nbsp;/, ' '),
        url: entry.draft ? url : entry.url,
      };
    } catch (error) {
      return getOembed(url, title);
    }
  }));
}

function getOembed(url, title) {
  return request('/api/content/oembed', { query: { url }, opts: { handleError: false } }).then(data => {
    return {
      title: title || data.title,
      url: normalizeUrl(url)
    };
  }).catch((err) => {
    return err.response.json().then(error => {
      return new Error(error.message);
    });
  });
}
