var { get: getPath } = require('lodash');
var moment = require('moment-timezone');
var tokenizer = require('sbd');
var isPublished = require('../lib/is_published');
var toRichText = require('../../shared/to_rich_text');
var toPlaintext = require('convert-rich-text').toPlaintext;
var fallback = require('../../shared/fallback');
var sentenceCase = require('../../shared/sentence_case');
var queryRegex = require('../../shared/query_regex');
var getPackageStories = require('../../shared/get_package_stories');
var autosave = require('./autosave');
var contributorActions = require('./contributors');
var docActions = require('./docs');
var videoActions = require('./videos');
var modalActions = require('./modals');
var groupActions = require('./groups');
var isPackageMember = require('../../shared/is_package_member');

export async function searchStories(q, isCoverStory, params = {}) {
  var toc = app.state.get(['doc', 'head', 'contents', 'draft', 'package_toc']);
  var cover = app.state.get(['doc', 'head', 'contents', 'draft', 'package_cover']);
  var packageStories = getPackageStories(toc);
  var searchParams = {
    q,
    order: 'score',
    schema_name: 'story',
    page: 1,
    per_page: 30,
    archived: false,
    trashed: false,
    story_type_blocklist: ['customstory', 'package'],
    includes: 'owner,community',
    ...params
  };
  if (isCoverStory) {
    // only search package stories that haven't already been added to the cover
    searchParams.uuids = packageStories.reduce((uuids, story) => {
      if (!cover.find(c => c.story && c.story.uuid === story.uuid)) {
        uuids.push(story.uuid);
      }
      return uuids;
    }, []).join(',');
  }

  return docActions.searchDocs(searchParams).then(({ docs }) => {
    if (docs.length === 0) {
      var text = 'No results found.';
      if (isCoverStory) {
        text = `
          <span class='m-package-cover__no-results'>
            No results. Try a new search or add more stories to this package.
          </span>
        `;
      }
      return [{ value: null, text }];
    }
    return docs.map(doc => formatResult(doc, q, isCoverStory));
  });
}

export function createStoryAndRedirect(params) {
  return docActions.createDoc('story', params)
    .then(doc => {
      // re-retrieve the doc to make sure we have all its attributes
      return docActions.getDoc(doc.id);
    })
    .then(doc => {
      var story = storyFromDoc(doc);
      return buildAndSavePackageStoryMembership(doc, story);
    })
    .then(doc => document.location.href = `/compose/${doc.uuid}`);
}

export function addStory(story) {
  return docActions.getDoc(story.uuid)
    .then(doc => buildAndSavePackageStoryMembership(doc, story));
}

function buildAndSavePackageStoryMembership(doc, story) {
  var toc = app.state.get(['doc', 'head', 'contents', 'draft', 'package_toc']);

  // If the story has already been added, do nothing
  if (getPackageStories(toc).find(s => s.uuid === story.uuid)) {
    return;
  }

  var packageId = app.state.get(['doc', 'id']);
  var delta = [];

  // if this is the first time we're adding a story, add a section too
  // otherwise, add the story to the end of the last section
  if (toc.length === 0) {
    delta.push({ p: ['draft', 'package_toc', 0], li: { stories: [story] } });
  } else {
    var sectionIndex = toc.length - 1;
    var storyIndex = toc[sectionIndex].stories.length;
    delta.push({ p: ['draft', 'package_toc', sectionIndex, 'stories', storyIndex], li: story });
  }

  // 1. load the doc into app state
  // 2. autosave its package_uuid
  // 3. autosave the story into the package_toc
  return autosave.setAndInitDoc(doc)
    .then(() => setPackageStoryMembership(doc))
    .then(() => autosave.resolveOnLocalUpdate(packageId, delta))
    .then(() => doc);
}

function setPackageStoryMembership(doc) {
  var docIsPublished = isPublished(doc.status);
  var delta = [{ p: ['package_uuid'], oi: app.state.get(['doc', 'uuid']) }];
  // If the doc is unpublished, make sure autotweets are unselected
  if (!docIsPublished) {
    delta.push(
      { p: ['draft', 'autotweets', 0, 'selected'], oi: false },
      { p: ['draft', 'autotweets', 1, 'selected'], oi: false }
    );
  }
  // Make sure the front page group is set to 'Do not place'
  return groupActions.loadGroups('site_group').then(groups => {
    var frontPageData = groups.find(g => g.slug === 'front-page');
    var siteGroups = doc.head.contents.draft.site_groups;
    if (!siteGroups) {
      // Initialize the site_group data but make sure the placement_slot value is -1
      var frontPageGroup = groupActions.groupFromData(frontPageData);
      frontPageGroup.placement_slot = -1;
      delta.push(
        { p: ['draft', 'primary_site_group_id'], oi: frontPageGroup.id },
        { p: ['draft', 'site_groups'], oi: [frontPageGroup] }
      );
    } else if (!docIsPublished) {
      // If group data exists and the doc is unpublished, find the front page group
      // and update its placement_slot value to -1
      var frontPageIndex = siteGroups.findIndex(group => group.id === frontPageData.id);
      if (frontPageIndex > -1 && siteGroups[frontPageIndex].placement_slot !== -1) {
        delta.push({ p: ['draft', 'site_groups', frontPageIndex, 'placement_slot'], oi: -1 });
      }
    }
    return autosave.resolveOnLocalUpdate(doc.id, delta);
  });
}

export function removeStory(uuid) {
  // we preserve the object containing the `heading` and `stories` array even if `stories` is empty)
  var packageId = app.state.get(['doc', 'id']);
  var packageUuid = app.state.get(['doc', 'uuid']);
  var { sectionIndex, storyIndex, story } = findStory(uuid);
  var storyId = app.state.get(['packageDocs', story.uuid, 'id']);
  var packageDelta = [{ p: ['draft', 'package_toc', sectionIndex, 'stories', storyIndex], ld: story }];
  var storyDelta = [{ p: ['package_uuid'], od: packageUuid }];

  // If the story existed in the cover, remove that cover item
  var cover = app.state.get(['doc', 'head', 'contents', 'draft', 'package_cover']);
  var coverIndex = cover.findIndex(c => c.story && c.story.uuid === story.uuid);
  if (coverIndex > -1) {
    packageDelta.push({ p: ['draft', 'package_cover', coverIndex], ld: cover[coverIndex] });
  }

  return autosave.resolveOnLocalUpdate(storyId, storyDelta)
    .then(() => autosave.resolveOnLocalUpdate(packageId, packageDelta))
    .then(() => autosave.teardownDoc(storyId));
}

function findStory(uuid) {
  var storyIndex = -1;

  var toc = app.state.get(['doc', 'head', 'contents', 'draft', 'package_toc']);
  var sectionIndex = toc.findIndex(section => {
    storyIndex = section.stories.findIndex(story => story.uuid === uuid);
    return storyIndex > -1;
  });

  var story = getPath(toc, [sectionIndex, 'stories', storyIndex]);
  return { sectionIndex, storyIndex, story };
}

export function addSection() {
  var toc = app.state.get(['doc', 'head', 'contents', 'draft', 'package_toc']);
  var packageId = app.state.get(['doc', 'id']);
  var delta = [];
  // If first heading doesn't exist, add it to create the first section
  if (toc.length === 1 && !toc[0].heading) {
    delta.push({
      p: ['draft', 'package_toc', 0, 'heading'],
      oi: toRichText('Untitled')
    });
  } else {
    delta.push({
      p: ['draft', 'package_toc', toc.length],
      li: { heading: toRichText('Untitled'), stories: [] }
    });
  }
  autosave.localUpdate(packageId, delta);
}

export function removeSection(index) {
  var toc = app.state.get(['doc', 'head', 'contents', 'draft', 'package_toc']);
  var packageId = app.state.get(['doc', 'id']);
  var delta = [];
  // removing section only removes the section title
  // if removing the only section, set the heading to an empty string
  // otherwise, all stories within removed section are added to end of previous section
  if (toc.length === 1) {
    delta.push({
      p: ['draft', 'package_toc', 0, 'heading'],
      od: toc[0].heading
    });
  } else {
    var section = toc[index];
    // handle case where user removes first section
    var newSectionIndex = index === 0 ? index + 1 : index - 1;
    var newSection = toc[newSectionIndex];
    var stories = newSection.stories.concat(section.stories);
    delta.push(
      { p: ['draft', 'package_toc', newSectionIndex, 'stories'], od: newSection.stories, oi: stories },
      { p: ['draft', 'package_toc', index], ld: section }
    );
  }
  autosave.localUpdate(packageId, delta);
}

export function moveSection(oldIndex, newIndex) {
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'package_toc', oldIndex], lm: newIndex }]);
}

export function moveStory(story, oldPath, newPath) {
  var docId = app.state.get(['doc', 'id']);
  var delta = [
    { p: oldPath, ld: story },
    { p: newPath, li: story }
  ];
  autosave.localUpdate(docId, delta);
}

export function addCoverComponent(type, component) {
  var cover = app.state.get(['doc', 'head', 'contents', 'draft', 'package_cover']);

  // If the component is a story and it has already been added, do nothing
  if (type === 'story' && cover.find(c => c.story && c.story.uuid === component.uuid)) {
    return;
  }

  var docId = app.state.get(['doc', 'id']);
  var index = cover.length;
  if (type === 'story') {
    component.layout = index === 0 ? 'horizontal' : '1col';
    autosave.localUpdate(docId, [{ p: ['draft', 'package_cover', index], li: { story: component } }]);
  } else if (type === 'pullquote') {
    autosave.localUpdate(docId, [{ p: ['draft', 'package_cover', index], li: { pullquote: toRichText('') } }]);
  } else if (type === 'video') {
    videoActions.selectVideo({ docId, path: ['draft', 'package_cover', index] });
  }
}

export function removeCoverComponent(component, index) {
  autosave.localUpdate(app.state.get(['doc', 'id']), [{
    p: ['draft', 'package_cover', index],
    ld: component
  }]);
}

export function moveCoverComponent(oldIndex, newIndex) {
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'package_cover', oldIndex], lm: newIndex }]);
}

export function editPackageSettings() {
  // set the history to false for when we return to the modal after inserting an image
  modalActions.pushModal({ editPackageSettings: true }, {});
}

export function packageStoryTitle(story, contents) {
  return toPlaintext(story.title) || packageStoryOriginalTitle(contents);
}

export function packageStoryOriginalTitle(contents) {
  return toPlaintext(fallback(contents, ['draft', 'internal_title'], ['draft', 'hed'])) || 'Untitled';
}

function formatResult(doc, q = '', isCoverStory) {
  // case-insensitive regex for search terms
  var regex = queryRegex(q);

  // 1. Title: highlight search terms
  var title = highlightKeywords(doc.title, regex);
  title = `<span class="p-story__result__title">${title}</span>`;

  // 2. Body: sanitize html and find the first sentence with highlighted search terms,
  // otherwise include first sentence
  var bodyDelta = doc.head.contents.draft.body;
  var sentences = tokenizer.sentences(toPlaintext(bodyDelta), { sanitize: true });
  var keywordSentence = sentences.find(s => regex.test(s));
  var body = keywordSentence ? highlightKeywords(keywordSentence, regex) : sentences[0];

  // Display warning in place of dek body if result already belongs to a package
  var disableResult = !isCoverStory && isPackageMember(doc.head.contents);
  if (disableResult) {
    body = `
      <span class='p-story__result__warning'>
        <span><res-icon name='warning' size='medium'/></span>
        <span>This story is part of another package.</span>
      </span>
    `;
  }

  if (body) {
    body = `<p class="p-story__result__dek">${body}</p>`;
  }

  // 3. Metadata: community of origin, byline, and publish date
  var byline = contributorActions.contributorsForDoc(doc);
  var date = moment(doc.updated_at).format(app.config.dateFormat);
  var status = sentenceCase(doc.status.toLowerCase());

  var metadata = '';
  if (doc.community_id !== app.state.get(['community', 'id'])) {
    metadata = `<span>${doc.community.name}</span>`;
  }
  metadata += `
    <span class="p-story__result__status ${status.toLowerCase()}">${status}</span>
    by <span class="p-story__result__byline">${byline}</span>
    on <span class="p-story__result__date">${date}</span>
  `;

  var additionalText = body ? body + metadata : metadata;

  return {
    text: title,
    additional_text: additionalText,
    value: storyFromDoc(doc),
    disabled: disableResult,
  };
}

function storyFromDoc(doc) {
  return {
    uuid: doc.uuid,
    entry_id: doc.entry_id,
    title: toRichText('')
  };
}

function highlightKeywords(text, regex) {
  return text.replace(regex, (match) => `<span class="p-story__result__highlight">${match}</span>`);
}
