var { last, isEqual } = require('lodash');

module.exports = function(path, delta) {
  const myPrefix = path.slice();
  let myIndex = myPrefix.pop();

  for (let op of delta) {
    const opPrefix = op.p.slice(0, op.p.length - 1);
    const opIndex = last(op.p);

    if (!isEqual(myPrefix, opPrefix)) {
      continue;
    }

    if (opIndex === myIndex && ('lm' in op)) {
      // this index was moved elsewhere
      myIndex = op.lm;
    } else if (opIndex === myIndex && ('ld' in op) && !('li' in op)) {
      // this index was removed, so the cursor is now invalid
      return null;
    } else if (opIndex < myIndex && op.lm >= myIndex) {
      // an element before us was moved after us
      myIndex -= 1;
    } else if (opIndex > myIndex && op.lm <= myIndex) {
      // an element after us was moved before us
      myIndex += 1;
    } else if (opIndex <= myIndex && ('li' in op) && !('ld' in op)) {
      // an element before us was added
      myIndex += 1;
    } else if (opIndex <= myIndex && ('ld' in op) && !('li' in op)) {
      // an element before us was removed
      myIndex -= 1;
    }
  }

  return myPrefix.concat([myIndex]);
};
