var twitter = require('twitter-text');

// Return Twitter-certified validation metadata for the provided autotweet text
export default function getTweetValidation(text) {
  // Override default max length of 280 chars with our own, which is set to 256
  // to account for the story url that will be auto-appended to the tweet. Logic
  // looks like:
  //
  // - Shortened url is 23 chars long, always
  // - Add 1 char for whitespace b/t tweet text and the url
  // - 280 - 24 = 256
  var config = Object.assign({}, twitter.configs.version2, {
    maxWeightedTweetLength: app.config.autotweet.max_char_count
  });

  return twitter.parseTweet(text, config);
}
