var imageActions = require('./images');
var modalActions = require('./modals');
var autosave = require('./autosave');
var { findIndex, sortBy } = require('lodash');

function galleryImageIndex(image) {
  return findIndex(app.state.get(['subdoc', 'head', 'contents', 'images']), (i) => i.id === image.id);
}

export function bulkEditImages(images) {
  app.state.set(['galleryEditImage'], null);
  var data = { editGalleryImages: { images } };
  if (modalActions.getModal('editGalleryImages')) {
    modalActions.swapModal(data);
  } else {
    modalActions.pushModal(data);
  }
}

export function hasGalleryImage(image) {
  return galleryImageIndex(image) > -1;
}

export async function getAndEditImage(image) {
  app.state.set(['galleryEditImage'], image);
  await imageActions.getAndEditImage(image, 'image_gallery');
}

export function insertGalleryImage(data) {
  var image = imageActions.imageFromData(data);
  // get the image we're replacing from the overlay state
  var oldImage = app.state.get(['galleryEditImage']);

  var index;
  // if the image exists in the gallery, replace it at its index
  if (oldImage) {
    index = galleryImageIndex(oldImage);
    app.state.set(['galleryEditImage'], image);
  }
  // if image doesn't currently exist in the gallery, insert at the end
  if (index == null || index === -1) {
    index = app.state.get(['subdoc', 'head', 'contents', 'images']).length;
  }

  var docId = app.state.get(['subdoc', 'id']);
  var delta = [{ p: ['images', index], ld: oldImage, li: image }];
  delta = delta.concat(imageActions.buildImageHistoryDelta(docId, image.id));

  autosave.localUpdate(docId, delta);
}

export function insertGalleryImages(images) {
  var docId = app.state.get(['subdoc', 'id']);
  var index = app.state.get(['subdoc', 'head', 'contents', 'images']).length;

  var delta = [];
  for (let image of images) {
    delta = delta.concat(
      [{ p: ['images', index], li: imageActions.imageFromData(image) }],
      imageActions.buildImageHistoryDelta(docId, image.id)
    );

    index = index + 1;
  }

  autosave.localUpdate(docId, delta);
}

export function removeGalleryImages(images) {
  var allImages = app.state.get(['subdoc', 'head', 'contents', 'images']);
  var delta = [];

  // map each image to its index in the full array of images, then sort those indices
  var indices = sortBy(images.map((image) => galleryImageIndex(image)));

  // in order to preserve the index as we delete the images, we traverse
  // the indices backwards to create the delta that removes all of them
  for (let index of indices.reverse()) {
    if (index > -1) {
      var image = allImages[index];
      delta.push({ p: ['images', index], ld: image });
    }
  }

  if (delta.length > 0) {
    autosave.localUpdate(app.state.get(['subdoc', 'id']), delta);
  }
}

export function reverseGalleryImages() {
  var images = app.state.get(['subdoc', 'head', 'contents', 'images']);
  autosave.localUpdate(app.state.get(['subdoc', 'id']), [{
    p: ['images'],
    od: images,
    oi: images.slice().reverse()
  }]);
}

export function moveGalleryImage(oldIndex, newIndex) {
  autosave.localUpdate(app.state.get(['subdoc', 'id']), [{ p: ['images', oldIndex], lm: newIndex }]);
}

export function bulkUpdateGalleryImages(images, updates) {
  // save the updated data to the images API as well
  return Promise.all(images.map((image) => {
    return imageActions.saveImage(Object.assign({}, image, updates));
  })).then(() => {
    var delta = [];

    for (let image of images) {
      var index = galleryImageIndex(image);
      if (index === -1) {
        // If this image is no longer present, then it must have been
        // simultaneously deleted by another user. Insert it at the end.
        var data = Object.assign({ url: image.src }, image, updates);
        insertGalleryImage(data);
      } else {
        for (let path of Object.keys(updates)) {
          delta.push({ p: ['images', index, path], od: image[path], oi: updates[path] });
        }
      }
    }

    if (delta.length > 0) {
      autosave.localUpdate(app.state.get(['subdoc', 'id']), delta);
    }
  });
}
