var getPath = require('lodash').get;
var toPlaintext = require('convert-rich-text').toPlaintext;

module.exports = function(contents) {
  const paths = [].slice.call(arguments, 1);
  for (let [i, path] of paths.entries()) {
    const value = getPath(contents, path);

    // return the value if
    // it's a non-empty Delta OR
    // it's not a Delta and is non-null OR
    // it's the last thing we're checking
    if ((value && 'ops' in value && toPlaintext(value)) ||
        (value && !('ops' in value)) ||
        (i === paths.length - 1)) {
      return value;
    }
  }
};
