var moment = require('moment-timezone');
var { sortBy } = require('lodash');
var request = require('../lib/request');
var parsePagination = require('../lib/parse_chorus_pagination');
var toPlaintext = require('convert-rich-text').toPlaintext;
var hash = require('../../shared/content-hash');

export function getLeagues() {
  var query = { community_id: app.state.get(['community', 'id']) };
  return request('/api/chorus/structured_tags/auto_taggable_leagues', {
    query: query
  }).then(({ leagues }) => sortBy(leagues, 'name'));
}

export function searchTags(type, query) {
  var community = app.state.get(['community']);
  var payload = {
    text: query,
    type: type,
    community_id: community.id
  };
  if (type !== 'live_coverage_event' && community.autoTagging.leagueRequired) {
    payload.league_id = app.state.get(['doc', 'head', 'contents', 'autotag_league_id']);
  }

  return request('/api/chorus/structured_tags/autocomplete', {
    query: payload
  }).then(data => {
    var tags = data.structured_tags;
    if (!tags.length) {
      return [{ value: null, text: 'No tags found.' }];
    }

    return tags.map(tag => {
      var name = nameWithScope(tag, type);
      return {
        text: name,
        value: {
          name: name,
          id: tag.id,
          disabled: false
        }
      };
    });
  });
}

export function suggestLinksAndTags() {
  var communityId = app.state.get(['community', 'id']);
  var leagueId = app.state.get(['doc', 'head', 'contents', 'autotag_league_id']);
  var leagueRequired = app.state.get(['community', 'autoTagging', 'leagueRequired']);
  var body = toPlaintext(app.state.get(['doc', 'head', 'contents', 'draft', 'body']));

  var payload = { text: body };
  if (leagueRequired) {
    payload.league_id = leagueId;
  }

  return request('/api/chorus/structured_tags/suggest_tags', {
    method: 'POST',
    body: payload,
    query: { community_id: communityId },
    opts: { handleError: false }
  }).then(({ structured_tags: tags }) => {
    // Filter out for blank tags
    var data = tags.filter(tag => tag.name !== null);
    return [formatLinks(data), formatTags(data)];
  }).catch(() => {
    return Promise.reject();
  });
}

// Only get events if valid for community && at least the league_id filter exists
export function getEvents(query) {
  var communityId = app.state.get(['community', 'id']);
  var status = app.state.get(['doc', 'head', 'contents', 'autotag_event_status']);
  var leagueId = app.state.get(['doc', 'head', 'contents', 'autotag_league_id']);
  var teamId = app.state.get(['doc', 'head', 'contents', 'autotag_team_id']);
  var leagueRequired = app.state.get(['community', 'autoTagging', 'leagueRequired']);
  var autotagTypes = app.state.get(['community', 'autoTagging', 'autotagTagTypes']);

  if (leagueRequired && autotagTypes.indexOf('event') > -1 && leagueId) {
    query.league_id = leagueId;
    query.community_id = communityId;
    query.per_page = 50;

    if (teamId) {
      query.team_id = teamId;
    }
    if (status) {
      query.status = status;
      if (status === 'upcoming') {
        query.order = 'asc';
      }
    }

    return request('/api/chorus/structured_tags/events', {
      method: 'GET',
      query: query
    }).then(result => {
      var events = formatEvents(result._embedded.event);
      var pagination = parsePagination(result);
      return { events, pagination };
    });
  } else {
    return Promise.resolve({ events: [], pagination: null });
  }
}

function formatEvents(events) {
  var timezone = app.state.get(['user', 'timeZone']);
  return events.map(event => {
    return {
      id: event.id,
      name: event.name,
      title: event.title,
      date: moment.tz(event.event_time, timezone).format(`${app.config.dateFormat}[,] h:mm zz`),
      location: event.location,
      outcome: getEventOutcome(event),
      disabled: false
    };
  });
}

// Parses event outcome, and only returns if the event is completed
function getEventOutcome(event) {
  var homeScore = event.home_team.score;
  var awayScore = event.away_team.score;

  if (typeof homeScore !== 'number' || typeof awayScore !== 'number') {
    return null;
  } else if (moment(event.event_time).isAfter(moment())) {
    return null;
  } else {
    return `${event.home_team.name} ${homeScore} &ndash; ${awayScore} ${event.away_team.name}`;
  }
}

function formatTags(data) {
  var autotagTypes = app.state.get(['community', 'autoTagging', 'autotagTagTypes']);
  // filter out tags with no id (these are links) or that aren't in our community's tag types
  var tags = data.filter(tag => tag.hasOwnProperty('id') && autotagTypes.includes(tag.type));

  var result = {};
  for (let tag of tags) {
    if (!result[tag.type]) {
      result[tag.type] = [];
    }
    result[tag.type].push({
      name: nameWithScope(tag, tag.type),
      id: tag.id,
      disabled: false
    });
  }

  return result;
}

function formatLinks(data) {
  // filter out SBN types from autolinks
  var excludedTypes = ['team', 'player', 'event'];
  var autolinkTypes = app.state.get(['community', 'autoTagging', 'autolinkTagTypes']).filter(tag => !excludedTypes.includes(tag));

  // filter out tags with no link or that aren't in our community's tag types
  var links = data.filter(tag => tag.hasOwnProperty('link') && autolinkTypes.includes(tag.type));

  return links.map(link => {
    return {
      href: parseLinkUrl(link.url),
      id: link.id ? link.id.toString() : hash(link),
      offset: link.link.offset,
      text: link.link.text,
      disabled: false
    };
  });
}

function parseLinkUrl(url) {
  var community = app.state.get(['community']);
  switch (community.network.id) {
  case 2:
  // Verge links have iconsistent domains
    return `https://${community.schemaDomain}/${url.split(/http[s]?:\/\/[^/]+\//)[1]}`;
  case 4:
  // Polygon links are just slugs, all Polygon-network communities use the same domain
    return `https://${community.network.schemaDomain}${url}`;
  default:
    return url;
  }
}

function nameWithScope(tag, type) {
  var name = tag.name;
  if (tag.scope && name.indexOf(tag.scope) < 0) {
    if (type === 'team') {
      name = `${tag.scope} ${name}`;
    } else {
      name = `${name} [${tag.scope}]`;
    }
  }
  return name;
}
