const messages = require('../config/messages');
const request = require('../lib/request');

export async function getEmbedContent(params) {
  return request('/api/frameable', {
    query: params,
    opts: { handleError: true }
  }).then(result => {
    if (result.status < 200 || 400 <= result.status) {
      console.warn(`Could not validate url: ${params.url}\nStatus: ${result.status}\nReason: ${result.error}`);
      throw new Error(messages.PYM_EMBED_SUPPORT_ERROR);
    }

    return result;
  });
}
